import React from "react";

class MySlideshow extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      items: this.props.items,
      id_active: 0,
      auto: true,
      intervalId: ""
    };
    /*Auto run*/
  }
  componentDidMount() {
    this.setAutoPlay();
  }
  autoPlay() {
    /*Autotrue ->Flase */
    if (this.state.auto) {
      this.setState({ auto: false });
      clearInterval(this.state.intervalId);
    } else {
      /*False->True*/
      this.setState({ auto: true });
      this.setAutoPlay();
    }
  }
  setAutoPlay() {
    var autoplay = setInterval(() => this.nextSlide(), 3000);
    this.setState({ intervalId: autoplay });
  }
  /*Dot nav click*/
  DotsClick(item) {
    /*reset*/
    this.UpdateSlideView(item.id);
  }
  /*Reuse*/
  UpdateSlideView(id) {
    for (var i = 0; i < this.state.items.length; i++) {
      var el = this.state.items[i];
      if (el.id !== id) {
        el.active = false;
      } else {
        el.active = true;
      }
    }
    this.setState({ items: this.state.items, id_active: id });
  }
  /*NextSlide*/
  nextSlide() {
    var nextId = this.state.id_active + 1;
    /*MaxItem*/
    if (nextId >= this.state.items.length) {
      nextId = 0;
    }
    this.UpdateSlideView(nextId);
  }
  /*PrevSlide*/
  prevSlide() {
    var prevId = this.state.id_active - 1;
    /*MaxItem*/
    if (prevId < 0) {
      prevId = this.state.items.length - 1;
    }
    this.UpdateSlideView(prevId);
  }
  render() {
    const liSlide = this.state.items.map((item) => (
      <li key={item.id} className={item.active ? "active" : ""}>
        {item.name}
        <br />
        <img src={item.img} alt={item.name} />
      </li>
    ));
    const liDots = this.state.items.map((item) => (
      <li
        key={item.id}
        onClick={() => this.DotsClick(item)}
        className={item.active ? "active" : ""}
      >
        {item.id}
      </li>
    ));
    return (
      <div className="slideWrapper">
        <h1>BÀI TẬP MY SLIDESHOW</h1>
        <ul className="ulSlides">{liSlide}</ul>
        <button onClick={() => this.prevSlide()}>Prev</button>
        <button onClick={() => this.nextSlide()}>Next</button>
        <button
          onClick={() => this.autoPlay()}
          className={this.state.auto ? "active" : ""}
        >
          AutoPlay
        </button>
        <ul className="ulDots">{liDots}</ul>
      </div>
    );
  }
}
export default MySlideshow;
